.header-page {
    position: relative;
    background-color: $color-background;
    z-index: 12;


    .header_container {
        @extend .container;
        display: flex;
        padding-top: 25px;
        padding-bottom: 39px;
        position: relative;
        z-index: 11;
    }

    .nav_section {
        display: flex;
        width: 100%;
    }

    .nav_list {
        display: flex;
        align-items: center;
        margin-right: auto;
        margin-left: auto;
        margin-top: 5px;
        gap: 45px;

        a {
            text-decoration: none;
            color: $color-text;
            font-size: 16px;
            line-height: 146%;
            position: relative;
        }

        a:hover {
            color: $color-primary;
            transition: .2s;
        }

        a:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            height: 2px;
            width: 0px;
            background-color: $color-primary;
        }

        a:hover:before {
            width: 100%;
            transition: .2s;
        }
    }

    .nav_btn {
        padding: 13px 26px;
    }

    .hamburger {
        padding: 0;
    }

    .hamburger-box {
        width: 28px;
    }

    .hamburger-inner {
        margin-top: 0px;
        &:before {
            top: -11px;
        }
        &:after {
            bottom: -11px;
        }
    }

    .hamburger.is-active .hamburger-inner,
    .hamburger.is-active .hamburger-inner:after,
    .hamburger.is-active .hamburger-inner:before {
        background-color: $color-text;
    }

    .hamburger-inner,
    .hamburger-inner:after,
    .hamburger-inner:before {
        width: 28px;
        height: 2px;
        transition-duration: .1s;
        border-radius: 0px;
        background-color: $color-text;
    }

    .hamburger.is-active:hover .hamburger-inner,
    .hamburger.is-active:hover .hamburger-inner:after,
    .hamburger.is-active:hover .hamburger-inner:before,
    .hamburger:hover .hamburger-inner,
    .hamburger:hover .hamburger-inner:after,
    .hamburger:hover .hamburger-inner:before  {
        background-color: $color-primary;
    }

    .hamburger.is-active:hover,
    .hamburger:hover {
        opacity: 1;
    }
}

.logo {
    max-width: 172px;
}